.body {
  font-family: TT Lakes;
  margin: 0;
  padding: 0; }

.paragraph {
  margin: 0;
  padding: 0;
  font-size: 14px; }

.paragraph > .link {
  font-size: 14px; }

.ul {
  list-style-type: none;
  margin: 0;
  padding: 0; }

.link {
  text-decoration: none;
  font-size: 16px;
  color: black;
  margin: 0;
  padding: 0;
  cursor: pointer; }

input, textarea {
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: TT Lakes;
  outline: 0;
  border: none; }

/* decorations */
.decoration-blur {
  -webkit-filter: blur(10px);
          filter: blur(10px);
  z-index: 1; }

.decoration-pageShadow {
  background-color: rgba(248, 248, 248, 0.5);
  box-shadow: 0 0 70px 0 #b1b1b1; }

.decoration-colorGray {
  color: #7E7E82; }

.decoration-colorPink {
  color: #FF3E79; }

.decoration-highlight {
  position: relative; }

.decoration-highlight::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 25%;
  width: 4px;
  height: 50%;
  border-radius: 0 5px 5px 0;
  background-color: #41f6d6; }

.decoration-shadowRight {
  position: relative; }

.decoration-shadowRight::after {
  content: '';
  position: absolute;
  width: 45px;
  height: 100%;
  top: 10px;
  right: 0;
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 10%, #f8f8f8); }

.decoration-headerGradient {
  background: linear-gradient(to right, #F8F8F8, white); }

.decoration-arrow {
  position: relative; }

.decoration-arrow::after, .decoration-arrow::before {
  content: '';
  position: absolute;
  inset: 50%;
  width: 2px;
  height: 9px;
  background: #FF3E79; }

.decoration-arrow::before {
  transform: translateY(-7px) rotate(-45deg); }

.decoration-arrow::after {
  transform: translateY(-2px) rotate(45deg); }

.decoration-bottomLine {
  position: relative; }

.decoration-bottomLine::after {
  content: '';
  position: absolute;
  width: 94%;
  height: 2px;
  background: #EAEAEA;
  left: 3%;
  bottom: 0; }

/* icon-navigation */
.content {
  margin: 0 auto;
  max-width: 1120px;
  background-color: #F8F8F8;
  height: auto; }

.icon-navigation {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  background-color: #FFFFFF;
  padding: 18.6px 24px;
  align-items: center; }

.logo {
  width: 96.55px;
  height: 50.81px;
  margin-right: auto; }

.icon-navigation__leftSide {
  position: relative;
  display: flex;
  align-items: center;
  max-height: 40px;
  gap: 40px; }

.icon-navigation__leftSide::after {
  content: '';
  position: absolute;
  width: 2px;
  height: 32px;
  background: #EAEAEA;
  border-radius: 1px;
  left: 60px;
  top: 5px; }

.icon-navigation__rightSide {
  position: relative;
  display: flex;
  max-height: 40px;
  gap: 40px; }

.icon-navigation__rightSide::after {
  content: '';
  position: absolute;
  width: 2px;
  height: 32px;
  background: #EAEAEA;
  border-radius: 1px;
  left: 170px;
  top: 5px; }

.icon-navigation__callChatProfile {
  display: flex;
  gap: 15px; }

.icon-navigation__repairStatus {
  display: flex;
  gap: 15px; }

/* page-title */
.page-title {
  display: flex;
  justify-content: space-between;
  padding: 0 32px; }

.page-title__text {
  font-size: 28px; }

.page-title__buttons {
  display: flex;
  gap: 30px; }

.icon__text {
  font-weight: 500;
  margin: 0;
  padding: 0; }

/* navigation */
.link-navigation {
  padding-top: 0;
  padding-left: 32px; }

.link-navigation__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  row-gap: 10px;
  margin-bottom: 10px; }

.link-navigation__item {
  font-size: 16px;
  white-space: nowrap;
  padding: 8px 17px;
  min-height: 25px; }

.link-navigation__item > .link {
  color: #7E7E82; }

.link-navigation__item:hover {
  background-color: #FFFFFF;
  outline: 2px solid #B8FFEC;
  outline-offset: -2px;
  border-radius: 5px; }

.link-navigation__item:nth-child(6) {
  margin-right: auto; }

.link-navigation__item--active {
  background-color: #FFFFFF;
  outline: 2px solid #B8FFEC;
  outline-offset: -2px;
  border-radius: 5px; }

/* scrollbar */
.scrollbar {
  overflow-x: scroll;
  overscroll-behavior-inline: contain;
  -ms-scroll-snap-type: x mandatory;
      scroll-snap-type: x mandatory; }

.scrollbar::-webkit-scrollbar {
  height: 5px;
  background-color: #F8F8F8; }

.scrollbar::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background-color: #F8F8F8; }

/* main */
.article {
  padding: 10px 35px;
  padding-bottom: 40px; }

.article__specification {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  gap: 30px;
  padding: 10px 35px; }

.article__specification__info {
  flex-basis: 50%; }

.article__specification__text {
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.2px;
  margin-bottom: 5px; }

.article__specification__img {
  max-width: 100%;
  height: auto; }

/* brends devices prices */
.title__text {
  margin: 16px 0;
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase; }

.prices-navigation__explanation {
  margin-top: 10px;
  display: flex;
  flex-flow: column;
  gap: 20px;
  max-width: 425px; }

.link__button-prices {
  font-size: 16px;
  font-weight: 500; }

.img-arrow {
  padding: 0 10px;
  transform: translateY(2px); }

.table__prices {
  margin-bottom: 30px;
  font-size: 14px;
  width: 100%;
  border-collapse: collapse; }

.tbody__prices {
  background: white;
  border: 1px solid #f1f1f1;
  border-radius: 5px; }

.row__prices > .cell-prices:first-child {
  padding-left: 30px; }

.row__prices > .cell-prices:last-child {
  padding-right: 30px;
  align-content: end; }

.content__footer {
  display: flex;
  justify-content: space-between;
  font-size: 14px; }

.content__footer__item {
  width: 33%; }

.content__footer__item:last-child {
  text-align: end; }

/* sidebars */
.sidebar__menu {
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
  height: 100vh;
  background: white;
  z-index: 1; }

.swiper-pagination-bullet {
  transform: translateY(-25px);
  width: 12px;
  height: 12px;
  background: #B5B6BC; }

/* sidebar-burger */
.sidebar__menu--burger {
  justify-content: space-between;
  width: 320px;
  left: 0; }

.main__menu {
  margin-top: 30px;
  margin-bottom: auto;
  margin-right: auto; }

.menu__links {
  display: flex;
  flex-direction: column;
  gap: 30px; }

.menu__link {
  position: relative;
  padding: 25px 15px; }

.menu__link--active {
  position: relative;
  padding: 25px 15px;
  color: #7E7E82; }

.menu__link--active::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 25%;
  width: 4px;
  height: 50%;
  border-radius: 0 5px 5px 0;
  background-color: #41f6d6; }

.menu__link:hover::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 25%;
  width: 4px;
  height: 50%;
  border-radius: 0 5px 5px 0;
  background-color: #41f6d6; }

.menu__link:hover {
  color: #7E7E82; }

.footer__menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 10px;
  padding: 35px 25px; }

.phoneNumber {
  font-size: 24px;
  line-height: 32px;
  font-weight: bold;
  margin-top: 0;
  margin-bottom: 30px; }

.menu__language {
  display: flex;
  justify-content: flex-start;
  gap: 5px; }

.language__text {
  line-height: 24px;
  font-weight: 500; }

input[type="radio"] {
  display: none; }

input[type="radio"]:checked + label {
  color: #7E7E82; }

/* sidebar-call */
.sidebar__menu--call, .sidebar__menu--chat {
  position: absolute;
  right: 0;
  width: 500px; }

.sidebar__header {
  position: relative;
  padding: 0; }

.sidebar__header > .icon-button--close {
  position: absolute;
  top: 0;
  left: -90px;
  margin: 25px 15px; }

.sidebar__header > .page-title__text {
  margin: 0;
  padding: 25px 30px;
  height: 40px; }

.main__sidebar__menu {
  padding: 0 30px;
  font-weight: 500;
  display: flex;
  flex-flow: column;
  gap: 15px; }

.sidebar__menu__form {
  margin-top: 20px;
  display: grid;
  justify-content: stretch; }

.sidebar__menu__form > .swiper-navigation__item {
  width: 410px; }

.form__message {
  min-height: 120px; }

.sidebar__submit {
  cursor: pointer;
  font-size: 12px;
  font-weight: bold;
  color: white;
  text-transform: uppercase;
  background-color: transparent; }

/* modifiers */
.element--hide {
  display: none; }

.element-noScroll {
  height: 100vh;
  overflow: clip; }

/* media */
@media screen and (min-width: 1060px) {
  .body {
    display: grid;
    grid-template-columns: 320px minmax(500px, auto);
    justify-content: center; }
  .sidebar__menu--burger {
    position: sticky; }
  .icon-navigation > .icon-button--close {
    display: none; }
  .content__header .icon-navigation {
    display: none; }
  .link-navigation {
    padding: 0 32px; }
  .link-navigation__list {
    flex-wrap: wrap; }
  .link-navigation::after {
    display: none; }
  .body + .decoration-pageShadow {
    box-shadow: none; } }

@media screen and (max-width: 1060px) {
  .page-title__buttons {
    display: none; }
  .article {
    padding: 10px 25px;
    padding-bottom: 30px; }
  .article__specification {
    padding: 10px 25px;
    gap: 20px; }
  .page-title {
    padding-left: 25px; }
  .link-navigation {
    padding-left: 25px; }
  .link-navigation__list::after {
    background: linear-gradient(to right, rgba(255, 255, 255, 0) 90%, #f8f8f8 100%); } }

@media screen and (min-width: 760px) {
  .prices-navigation__container > .swiper {
    display: none; } }

@media screen and (max-width: 760px) {
  .table__prices {
    display: none; }
  .sidebar__header > .icon-button--close {
    position: relative;
    left: 0;
    margin: 25px 15px 0 15px; }
  .sidebar__header > .page-title__text {
    padding: 25px 15px;
    height: 40px; }
  .main__sidebar__menu {
    padding: 0 15px; }
  .sidebar__menu__form > .swiper-navigation__item {
    width: auto; } }

@media screen and (max-width: 530px) {
  .icon-navigation__rightSide > .icon-navigation__callChatProfile {
    display: none; }
  .icon-navigation__leftSide {
    gap: 15px; }
  .icon-navigation__leftSide::after {
    display: none; }
  .icon-navigation__rightSide::after {
    left: -17px; }
  .article {
    padding: 10px 15px;
    padding-bottom: 20px; }
  .article__specification {
    padding: 10px 0;
    flex-wrap: wrap; }
  .article__specification__info {
    padding: 0 15px;
    flex-basis: 100%; }
  .site__menu {
    width: 100%; }
  .icon-navigation {
    padding-left: 15px;
    gap: 15px; }
  .page-title {
    padding-left: 15px; }
  .link-navigation {
    padding-left: 15px; }
  .article__specification__text {
    line-height: 18px; }
  .article__specification__info > .navigation__list--hide {
    max-height: 90px; }
  .content__footer {
    flex-flow: column; }
  .content__footer__item {
    text-align: center;
    width: 100%;
    margin-top: 10px; }
  .content__footer__item:last-child {
    text-align: center; }
  .sidebar__menu--burger, .sidebar__menu--call, .sidebar__menu--chat {
    width: 100%; } }
